import React, { FC } from "react"
import * as classes from './EventItem.module.scss'
import { Button, Col, Row, } from "reactstrap"
import { ReportContainer } from "@/entities/EventReport"
import { EventItemResultType } from "../model/EventItemSlice"
import { Presentation } from "@/entities/EventPresentation"
import { Plan } from "@/entities/EventPlan"
import { Comment } from "@/entities/EventComment"
import 'bootstrap/dist/css/bootstrap.min.css';
import { useAppDispatch } from "@/app/lib/hooks/redux"
import { send } from "@/processes/event"
import { cancelResultMenu } from "../model/EventItemThunk"

interface EventItemProps {
    menuType: EventItemResultType,
    withPlan: boolean,

}




const EventItem: FC<EventItemProps> = ({
    menuType,
    withPlan,

}) => {
    const dispatch = useAppDispatch();
    const sendEvent = () => {
        dispatch(
            send()
        )
    }

    const cancel = () => {
        dispatch(
            cancelResultMenu()
        )
    }

    return <React.Fragment>

        <div className={classes.wrapper}>

            <Row className={`${classes.topArea} p-10`}>
                <Col sm={5} className="p-0">
                    {/* <Row> */}
                    <Col className={`${classes.cardWrapper} p-0`}>
                        <ReportContainer
                            isNoResult={false}
                        />
                    </Col>
                    {/* <Col className={classes.line} sm={1}></Col> */}
                    <Col className={`${classes.cardWrapper} mt-2 p-0`}>
                        <Comment />
                    </Col>
                    {/* </Row> */}
                </Col>
                {/* <Col sm={1} className={`${classes.space} p-0`}></Col> */}
                <Col sm={4} className={`${classes.space} `}>
                    {/* <Row> */}
                    <Col sm={12} className="p-0 m-0">
                        {menuType === 'result' || menuType === 'new'
                            ? <Presentation />
                            : <ReportContainer isNoResult={true} />
                        }
                        {/* {menuType === 'new'
                            ? <Presentation />
                            : <ReportContainer isNoResult={true} />
                        } */}
                    </Col>

                    {withPlan && <Col sm={12} className="mt-2 p-0">
                        <Plan />
                    </Col>}
                    {/* </Row> */}
                </Col>
                <Col className={`${classes.actionsWrapper} p-0`}>
                    <div className={classes.actions}>

                        <Button className={`${classes.btn_send} `} size='sm'
                            onClick={sendEvent}
                        >
                            
                        </Button>
                        <Button className={classes.btn_document+' mt-2'} size='sm'
                            onClick={sendEvent}
                        >
                            
                        </Button>

                        <Button className={`${classes.btn_cancel} mt-2`} size='sm'
                            onClick={cancel}
                        >
                            
                        </Button>
                    </div>

                </Col>
            </Row>
            {/* <Row className={`${classes.bottomArea}`}>
                <Col sm={5} className="p-0">
                    {menuType === 'result'
                        ? <Presentation />
                        : <ReportContainer isNoResult={true} />
                    }




                </Col >
                <Col className={classes.line} sm={1}></Col>
                {withPlan &&
                    <Col sm={5} className="p-0">
                        <Plan />
                    </Col>
                }
            </Row> */}
        </div>



        {/* </div> */}
    </React.Fragment >
}

export default EventItem